*,
*:before,
*:after {
  padding: 0;
  margin: 0;
  -webkit-box-sizing: inherit;
  -moz-box-sizing: inherit;
  box-sizing: inherit;
}

html {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  font-size: 100%;
}

html,
body {
  min-height: 100%;
}

h1 a,
h2 a,
h3 a,
h4 a,
h5 a,
h6 a {
  color: inherit;
}

ul {
  margin: 0;
  padding: 0;
}

ul li {
  list-style: none;
}

img {
  max-width: 100%;
  height: auto;
}

i {
  font-style: normal;
}

figure {
  margin: 0;
  padding: 0;
}

.clearfix:after {
  content: "";
  clear: both;
  display: table;
}

a,
a:active,
a:focus {
  text-decoration: none;
  outline: none;
  border: none;
  transition: all 0.4s ease;
  -webkit-transition: all 0.4s ease;
  -moz-transition: all 0.4s ease;
  -ms-transition: all 0.4s ease;
}

a:hover {
  text-decoration: none;
  outline: none;
  color: #eb9309;
  -webkit-transition: 0.3s;
  -o-transition: 0.3s;
  transition: 0.3s;
}

label {
  color: #666;
  cursor: pointer;
  font-size: 14px;
  font-weight: normal;
}

a:focus,
input:focus,
textarea:focus,
button:focus {
  outline: 0 solid;
}

body {
  font-family: "Barlow", sans-serif;
  color: #666666;
  font-size: 1rem;
}

p {
  line-height: 1.75;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "Oswald", sans-serif;
  color: #000;
}

button {
  cursor: pointer;
}

button.button {
  background: transparent;
  border: 0px;
  -webkit-transition: 0.3s;
  -o-transition: 0.3s;
  transition: 0.3s;
}

::-moz-selection {
  background: #313131;
  color: #fff;
  text-shadow: none;
}

::selection {
  background: #313131;
  color: #fff;
  text-shadow: none;
}

h1 {
  font-size: calc(1.55rem + 3.6vw);
}

@media (min-width: 1200px) {
  h1 {
    font-size: 4.25rem;
  }
}

h2 {
  font-size: calc(1.46875rem + 2.625vw);
}

@media (min-width: 1200px) {
  h2 {
    font-size: 3.4375rem;
  }
}

h3 {
  font-size: calc(1.40625rem + 1.875vw);
}

@media (min-width: 1200px) {
  h3 {
    font-size: 2.8125rem;
  }
}

h4 {
  font-size: calc(1.35625rem + 1.275vw);
}

@media (min-width: 1200px) {
  h4 {
    font-size: 2.3125rem;
  }
}

h5 {
  font-size: calc(1.31875rem + 0.825vw);
}

@media (min-width: 1200px) {
  h5 {
    font-size: 1.9375rem;
  }
}

h6 {
  font-size: calc(1.275rem + 0.3vw);
}

@media (min-width: 1200px) {
  h6 {
    font-size: 1.5rem;
  }
}

