.mask {
  mask-image: url(../image/shapes/mask-shape1.svg);
}

.mask--about {
  position: absolute;
  top: 0;
  z-index: -1;
  left: -11px;
  padding: 1px;
}

@media (max-width: 1199px) {
  .mask--about {
    left: -30%;
  }
}

@media (max-width: 991px) {
  .mask--about {
    display: none;
  }
}

.mask--about .mask__overlay {
  position: absolute;
  left: 2px;
  top: -1px;
}

.mask--review {
  mask-image: url(../image/shapes/testi-shape3.svg);
  bottom: 9%;
  right: 0;
  z-index: -1;
}

.mask--review .mask__overlay {
  position: absolute;
  left: -3px;
  top: -1px;
  transform: scale(1.007);
}

@media (max-width: 1199px) {
  .mask--review {
    display: none;
  }
}

.mask-aboutBack {
  position: absolute;
  left: 0;
  top: 66%;
  z-index: -11;
}

@media (max-width: 1199px) {
  .mask-aboutBack {
    left: -30%;
  }
}

@media (max-width: 991px) {
  .mask-aboutBack {
    display: none;
  }
}

.color-title {
  color: #0d0d0d;
}

@-webkit-keyframes toBottomFromTop {
  49% {
    -webkit-transform: translateY(-100%);
    transform: translateY(-100%);
  }
  50% {
    opacity: 0;
    -webkit-transform: translateY(100%);
    transform: translateY(100%);
  }
  51% {
    opacity: 1;
  }
}

@keyframes toBottomFromTop {
  49% {
    -webkit-transform: translateY(-100%);
    transform: translateY(-100%);
  }
  50% {
    opacity: 0;
    -webkit-transform: translateY(100%);
    transform: translateY(100%);
  }
  51% {
    opacity: 1;
  }
}

