.it-btn {
  z-index: 1;
  transition: color 0.5s;
  position: relative;
}

.it-btn__inner {
  z-index: -1;
  overflow: hidden;
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background: #eb9309;
  transition: all 0.4s ease;
  -webkit-transition: all 0.4s ease;
  -moz-transition: all 0.4s ease;
  -ms-transition: all 0.4s ease;
}

.btn--secondary .it-btn__inner {
  background-color: #7fb432;
}

.btn--black .it-btn__inner {
  background-color: #000;
}

.btn--icon .it-btn__inner {
  background: transparent;
}

.it-btn__blobs {
  position: relative;
  display: block;
  height: 100%;
  filter: url("#goo");
}

.it-btn__blob {
  position: absolute;
  width: 25%;
  height: 100%;
  background: #7fb432;
  border-radius: 100%;
  transform: translate3d(0, 150%, 0) scale(1.7);
  transition: transform 0.65s;
}

.btn--secondary .it-btn__blob {
  background-color: #eb9309;
}

.btn--black .it-btn__blob {
  background-color: #eb9309;
}

.btn--icon .it-btn__blob {
  background: #eb9309;
}

@supports (filter: url("#goo")) {
  .it-btn__blob {
    transform: translate3d(0, 150%, 0) scale(1.4);
  }
}

.it-btn__blob:nth-child(1) {
  left: 0%;
  transition-delay: 0s;
}

.it-btn__blob:nth-child(2) {
  left: 30%;
  transition-delay: 0.09s;
}

.it-btn__blob:nth-child(3) {
  left: 60%;
  transition-delay: 0.18s;
}

.it-btn__blob:nth-child(4) {
  left: 90%;
  transition-delay: 0.27s;
}

.it-btn:hover .it-btn__blob {
  transform: translateZ(0) scale(1.7);
}

@supports (filter: url("#goo")) {
  .it-btn:hover .it-btn__blob {
    transform: translateZ(0) scale(1.4);
  }
}

.it-btn__animation {
  position: absolute;
  bottom: 0;
  height: 100%;
  left: 0;
  width: 100%;
}

